import uniBadge from "@/components/uni-badge/uni-badge.vue"
import { request } from '../../request.js'
export default {
	data() {
		return {
			// swiper模拟数据
			imgUrl: [],
			navList: ["详情","评价","售后"],
			// 控制详情、评价、售后切换
			currentIndex: 0,
			// 控制评价类型
			appraiseIndex: 0,
			appraiseNav: [
				{
					type: 0,
					name: '全部评价6.2W'
				},
				{
					type: 1,
					name: '好评5K'
				},
				{
					type: 2,
					name: '中评2K'
				},
				{
					type: 3,
					name: '差评8'
				},
				{
					type: 4,
					name: '有图评价99'
				},
			],
			appraiseList: [],
			saleList: [
				{
					title: '厂家服务',
					des: '产品全国联保，享受三包服务，质保期为：一年质保。'
				},
				{
					title: '店下承诺',
					des: '店下平台卖家销售并发货的商品，由平台卖家提供发票和相应的售后服务您放心购买！'
				},
				{
					title: '正品行货',
					des: '凭质保证书及京东商城发票，可享受全国联保服务。',
				}
			],
			number: '99',
			isShow: false,
			id: '',
			dataList: '',
			// 邮费
			sales: '',
			
		};
	},
	onLoad(options) {
		// 存储商品的id
		let id = options.id;
		this.id = id;
		// 请求详情页数据
		request({
			url :'index.php?s=/wap/goods/Apigoodsdetail',
			method: 'POST',
			data: {
				id: id
			}
		}).then(res => {
			this.dataList = res.data.goods_detail;
			// 处理轮播图数据
			let imgUrl = res.data.goods_detail.img_list;
			for(let i = 0;i < imgUrl.length;i++) {
				imgUrl[i].pic_cover_big = 'http://lfs.086soft.cn/' + imgUrl[i].pic_cover_big
			}
			this.imgUrl = imgUrl
		});
		// 请求邮费
		request({
			url: 'index.php?s=/wap/Goods/getShippingFeeNameByLocation',
			method: 'POST',
			data: {
				goods_id: id
			}
		
		}).then(res => {
			
			if(typeof (res.data) == 'string') {
				this.sales = res.data
			}
			else {
				this.sales = res.data[0].express_fee;
			}
			// console.log()
		})
		// 请求评价(默认请求全部)
		request({
			url: 'index.php?s=/wap/Goods/getGoodsComments',
			method: 'POST',
			data: {
				goods_id: 11,
				comments_type: 0
			}
		}).then(res => {
			// 处理头像和评论图像的路径;
			let data = res.data.data
			for(let i = 0;i < data.length;i++) {
				// 格式化时间
				let totalNumber = new Date(data[i].addtime * 1000);
				let y = totalNumber.getFullYear();
				let m = totalNumber.getMonth() + 1 > 10?totalNumber.getMonth() + 1 : '0' + (totalNumber.getMonth() + 1);
				let d = totalNumber.getDay() > 10?totalNumber.getDay(): '0' + (totalNumber.getDay());
				data[i].addtime = y+'-'+m+'-'+d
				data[i].user_img = 'http://lfs.086soft.cn/' + data[i].user_img
				data[i].image = data[i].image.split(',');
				if(data[i].image.length > 0 && data[i].image != '') {
					for(let j = 0;j < data[i].image.length;j++) {
						data[i].image[j] = 'http://lfs.086soft.cn/' + data[i].image[j]
					}
				}			
			}
			this.appraiseList = data;
		})
	},
	methods: {
		// 切换详情、评价、售后
		handleClick(index) {
			this.currentIndex = index
		},
		// 切换评价类型
		handleAppraise(index) {
			this.appraiseIndex = index
		},
		// 点击加入购物车按钮
		handleCart() {
			this.isShow = true
		},
		// 点击关闭遮挡层
		close() {
			this.isShow = false
		},
		// 点击立即购买按钮跳转到付款页面
		handlePay() {
			uni.navigateTo({
				url:'/pages/components/pay/pay'
			})
		}
	},
	components: {uniBadge}
}
